:root {
  --port-gore: rgba(38, 29, 86, 1);
  --cyber-grape: rgba(75, 66, 121, 1);
  --victoria: rgba(85, 75, 134, 1);
  --victoria-2: rgba(87, 75, 144, 1);
  --sheen-green: rgba(109, 212, 0, 1);
  --waterloo-: rgba(129, 124, 155, 1);
  --medium-purple: rgba(148, 117, 255, 1);
  --soap: rgba(202, 198, 231, 1);
  --alizarin-crimson: rgba(224, 32, 32, 0.643);
  --alizarin-crimson-2: rgba(224, 32, 32, 1);
  --selective-yellow: rgba(247, 181, 0, 1);
  --blaze-orange: rgba(250, 100, 0, 0.753);
  --fiery-rose: rgba(253, 78, 113, 1);
  --tickle-me-pink: rgba(253, 121, 168, 1);
  --pink: rgba(255, 205, 205, 1);
  --white-2: rgba(255, 255, 255, 0.847);
  --white: rgba(255, 255, 255, 1);

  --font-size-s: 10px;
  --font-size-m: 12px;
  --font-size-l: 13px;
  --font-size-xl: 14px;
  --font-size-xxl: 15px;
  --font-size-xxxl: 16px;
  --font-size-xxxxl: 20px;
  --font-size-xxxxxl: 22px;
  --font-size-xxxxxxl: 24px;
  --font-size-xxxxxxxl: 26px;
  --font-size-xxxxxxxxl: 27px;
  --font-size-xxxxxxxxxl: 50px;
  --font-size-xxxxxxxxxxl: 57px;

  --font-family-poppins: "Poppins";
}
.border-1px-pink {
  border-width: 1px;
  border-style: solid;
  border-color: var(--pink);
}
.poppins-normal-waterloo--14px {
  color: var(--waterloo-);
  font-family: var(--font-family-poppins);
  font-size: var(--font-size-xl);
  font-weight: 400;
  font-style: normal;
}
.poppins-normal-victoria-10px {
  color: var(--victoria);
  font-family: var(--font-family-poppins);
  font-size: var(--font-size-s);
  font-weight: 400;
  font-style: normal;
}
.poppins-bold-alizarin-crimson-24px {
  color: var(--alizarin-crimson-2);
  font-family: var(--font-family-poppins);
  font-size: var(--font-size-xxxxxxl);
  font-weight: 700;
  font-style: normal;
}
.border-2px-white {
  border-width: 2px;
  border-style: solid;
  border-color: var(--white);
}
.poppins-normal-waterloo--12px {
  color: var(--waterloo-);
  font-family: var(--font-family-poppins);
  font-size: var(--font-size-m);
  font-weight: 400;
  font-style: normal;
}
.poppins-semi-bold-soap-15px {
  color: var(--soap);
  font-family: var(--font-family-poppins);
  font-size: var(--font-size-xxl);
  font-weight: 600;
  font-style: normal;
}
.poppins-semi-bold-cyber-grape-15px {
  color: var(--cyber-grape);
  font-family: var(--font-family-poppins);
  font-size: var(--font-size-xxl);
  font-weight: 600;
  font-style: normal;
}
.border-1px-medium-purple {
  border-width: 1px;
  border-style: solid;
  border-color: var(--medium-purple);
}
.poppins-medium-cyber-grape-15px {
  color: var(--cyber-grape);
  font-family: var(--font-family-poppins);
  font-size: var(--font-size-xxl);
  font-weight: 500;
  font-style: normal;
}
.poppins-normal-cyber-grape-12px {
  color: var(--cyber-grape);
  font-family: var(--font-family-poppins);
  font-size: var(--font-size-m);
  font-weight: 400;
  font-style: normal;
}
.poppins-medium-white-22px {
  color: var(--white);
  font-family: var(--font-family-poppins);
  font-size: var(--font-size-xxxxxl);
  font-weight: 500;
  font-style: normal;
}
.border-0-5px-pink {
  border-width: 0.5px;
  border-style: solid;
  border-color: var(--pink);
}
.border-1px-fiery-rose {
  border-width: 1px;
  border-style: solid;
  border-color: var(--fiery-rose);
}
